body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  margin: auto auto;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  align-items: center;
  height: 60vh;
  width: auto;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {

  font-size: large;

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.news-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;


}

.iron-image__container {

  height: 300px;
  width: 500px;


  background-size: 100% 100%;
}

.iron-image {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  height: 300px;
  width: 500px;

  opacity: 0;

  -webkit-transition: opacity 1s ease;

  transition: opacity 1s ease;
}

.iron-image--fade-in {
  opacity: 1;
}
button.infoButton {
  color: white;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50px;
  height: 10px;
  background: #3498DB;
  border-radius: 5px;
  -webkit-animation: load 0.8s ease-in-out infinite;
          animation: load 0.8s ease-in-out infinite; }
  .loader:before, .loader:after {
    position: absolute;
    display: block;
    content: "";
    -webkit-animation: load 0.8s ease-in-out infinite;
            animation: load 0.8s ease-in-out infinite;
    height: 10px;
    border-radius: 5px; }
  .loader:before {
    top: -20px;
    left: 10px;
    width: 40px;
    background: #EF4836; }
  .loader:after {
    bottom: -20px;
    width: 35px;
    background: #F5AB35; }

@-webkit-keyframes load {
  0% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px); }
  50% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px); }
  100% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px); } }

@keyframes load {
  0% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px); }
  50% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px); }
  100% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px); } }

